<template>
	<div id="memberProfile">
		<public-header title="管理员资料"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="账号" :value="plateformer.username" />
				<van-cell title="密码" value="修改" is-link :to="util.getUrl({path: '/pages/member/profilePassword'})"/>
			</van-cell-group>
			<div class="padding-15">
				<van-button type="primary" size="normal" class="font-15" block @click="onLogout">退出登录</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				plateformer: {
					username: ''
				}
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/member/mine',
					autoAssign: true,
					variable: 'plateformer'
				})
			},
			onLogout() {
				this.$dialog.confirm({
					title: '温馨提示',
					message: '确定退出登录吗？'
				}).then(() => {
					this.util.removeStorage('ipuser');
					this.util.removeStorage('iptoken');
					this.util.removeStorage('ipjpush_relation');
					this.$router.replace(this.util.getUrl({path: '/pages/auth/login'}));
					return;
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#memberProfile .content{
		padding-top: 10px;
	}
	#memberProfile .content .van-cell__value span{
		color:#666;
	}
	#memberProfile .content .submit{
		margin-top: 20px;
		padding: 0 15px;
	}
</style>



